import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppComponent } from './app.component';
import {DataTableModule, MultiSelectModule , SharedModule , DialogModule , ButtonModule , CalendarModule ,CheckboxModule ,InputTextModule,
        InputTextareaModule,   DropdownModule, InputSwitchModule , RadioButtonModule , 
        RatingModule, AccordionModule} from 'primeng/primeng';
import { RouterModule, Routes } from '@angular/router';
import {HomeComponent } from './pages/home/home.component';
import { PRComponent } from './pages/pr/pr.component';
import{router , routes } from './main/router.component'
import{EmployeeService} from './providers/commonServices'

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    PRComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    RouterModule,
    routes,
    MultiSelectModule,
    BrowserAnimationsModule,
    DialogModule,
    InputTextModule,
    ButtonModule,
    DataTableModule,
    SharedModule,
    CalendarModule,
    CheckboxModule,
    DropdownModule,
    InputSwitchModule,
    InputTextareaModule,
    RatingModule,
    RadioButtonModule,
    AccordionModule,
      RouterModule.forRoot(router, {useHash: false})
  ],
  providers: [EmployeeService],
  bootstrap: [AppComponent]
})
export class AppModule { }



